
import { defineComponent, watch } from 'vue'

export default defineComponent({
  props: {
    // 表格标题
    title: {
      type: String,
      default: '表格标题'
    },
    // 是否显示选择列
    showSelectionColumn: {
      type: Boolean,
      default: false
    },
    // 是否显示序号列
    showIndexColumn: {
      type: Boolean,
      default: false
    },
    // 表格数据
    tableData: {
      type: Array,
      required: true
    },
    // 表格字段
    propList: {
      type: Array
    },
    // 某些页面table特有的属性
    childrenProps: {
      type: Object,
      default: () => ({})
    },
    // 分页器总计
    dataTotal: {
      type: Number,
      default: 0
    },
    // 分页器的数据，从pageContent通过v-model传递
    page: {
      type: Object,
      default: () => ({ currentPage: 1, pageSize: 10 })
    },
    //是否显示footer
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  emits: ['selectionChange', 'update:pageSize', 'update:currentPage'],
  setup(props, { emit }) {
    // 1.表格相关
    // 多选框变化事件
    const handleSelectionChange = (value: any) => {
      emit('selectionChange', value)
    }

    // 2.分页器相关
    watch(
      () => props.page.pageSize,
      // (pageSize: number) => {
      //   console.log('update:pageSize')
      //   emit('update:pageSize', { ...props.page, pageSize })
      // }
      (pageSize: number) => {
        emit('update:pageSize', pageSize)
      }
    )
    watch(
      () => props.page.currentPage,
      // (currentPage: number) => {
      //   console.log('update:currentPage')
      //   emit('update:currentPage', { ...props.page, currentPage })
      // }
      (currentPage: number) => {
        emit('update:currentPage', currentPage)
      }
    )

    return {
      handleSelectionChange
    }
  }
})
